export default {
	data() {
		return {
			type: 1,
			messageData: "",
			page: 1,
			finished: false
		};
	},
	computed: {},
	methods: {
		toMessageDetail(id) {
			uni.navigateTo({
				url: `/pages/mine/sys-message/detail?id=${id}`
			})
		}
	},
	onLoad(e) {
		this.type = e.id
	},
	onShow() {
		this.$store.dispatch("getMessageList", {
			cid: this.type
		}).then(res => {
			this.messageData = res.data;
		}).catch(err => {
			this.finished = true
		});
	},
};
